<h3 className="">{"Fixed"}</h3>
{"\n"}
<Example>
    <mwc-button
        raised
        label="Open Menu"
        onClick={({ target }) => (target as HTMLElement).nextElementSibling.show()}
    />
    <mwc-menu fixed ref={menu => menu.anchor = menu.previousElementSibling}>
        <mwc-list-item>Item 0</mwc-list-item>
        <mwc-list-item>Item 1</mwc-list-item>
        <mwc-list-item>Item 2</mwc-list-item>
        <mwc-list-item>Item 3</mwc-list-item>
    </mwc-menu>
</Example>
