<h3 className="">{"Selection Groups"}</h3>
{"\n"}
<p className="">{"Adding a "}
<code className="">{"group"}</code>
{" to your "}
<code className="">{"mwc-list-item"}</code>
{"s will associate them with other\n"}
<code className="">{"[mwc-list-item]"}</code>
{"s in the same group and make selection function similarly to a radio group."}</p>
{"\n"}
<Example>
    <style>
        {`/* Hide the icon of unselected menu items that are in a group */
        #menu > [mwc-list-item][group]:not([selected]) [slot='graphic'] {
            display: none;
        }`}
    </style>
    <div style={{ position: 'relative' }}>
        <mwc-button
            raised
            label="Open Menu"
            onClick={({ target }) => (target as HTMLElement).nextElementSibling.show()}
        />
        <mwc-menu multi id="menu">
            <mwc-list-item group="a" graphic="icon">
                <mwc-icon slot="graphic">check</mwc-icon>
                <span>Item 0</span>
            </mwc-list-item>
            <mwc-list-item group="a" graphic="icon" selected>
                <mwc-icon slot="graphic">check</mwc-icon>
                <span>Item 1</span>
            </mwc-list-item>
            <mwc-list-item group="a" graphic="icon">
                <mwc-icon slot="graphic">check</mwc-icon>
                <span>Item 2</span>
            </mwc-list-item>
            <li divider role="separator"></li>
            <mwc-list-item group="b" graphic="icon" selected>
                <mwc-icon slot="graphic">check</mwc-icon>
                <span>Item 3</span>
            </mwc-list-item>
            <mwc-list-item group="b" graphic="icon">
                <mwc-icon slot="graphic">check</mwc-icon>
                <span>Item 4</span>
            </mwc-list-item>
        </mwc-menu>
    </div>
</Example>
