<h3 className="">{"Styled"}</h3>
{"\n"}
<p className=""><code className="">{"mwc-menu"}</code>
{" internally uses "}
<a href="components/content/list" className=""><code className="">{"mwc-list"}</code></a>
{",\nand all CSS custom properties exposed by "}
<code className="">{"mwc-list"}</code>
{" apply here as well."}</p>
{"\n"}
<Example>
    <style>
        {`#menu {
            --mdc-menu-min-width: 200px;
            --mdc-menu-item-height: 30px;
            --mdc-theme-surface: aliceblue;
            /* inherits the styles of mwc-list internally */
            --mdc-theme-primary: blue;
            --mdc-list-vertical-padding: 0px;
            --mdc-list-side-padding: 30px;
        }`}
    </style>
    <div style={{ position: 'relative' }}>
        <mwc-button
            raised
            label="Open Menu"
            onClick={({ target }) => (target as HTMLElement).nextElementSibling.show()}
        />
        <mwc-menu activatable multi id="menu">
            <mwc-list-item selected activated>
                Item 0
            </mwc-list-item>
            <mwc-list-item selected activated>
                Item 1
            </mwc-list-item>
            <mwc-list-item>Item 2</mwc-list-item>
            <mwc-list-item>Item 3</mwc-list-item>
        </mwc-menu>
    </div>
</Example>
